
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: -webkit-transform 0.2s linear;
  transition: -webkit-transform 0.2s linear;
  transition: transform 0.2s linear;
  transition: transform 0.2s linear, -webkit-transform 0.2s linear;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  opacity: 0;
}
.fade-enter,
.fade-leave-active {
  -webkit-transform: translateY(-30%);
          transform: translateY(-30%);
  opacity: 0;
}
.img-view {
  position: absolute;
  width: 100%;
  height: 100%;
}
.img-view .img-layer {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  background: rgba(0,0,0,0.9);
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.img-view .img-scale img {
  width: 10rem;
  display: block;
  position: absolute;
  height: 60%;
  left: 0;
  top: 20%;
  right: 0;
  margin: auto;
  z-index: 1000;
}


