
.activity-wrapper {
  overflow: hidden;
  width: 100%;
}
.activity-wrapper .activity-products {
  width: 140%;
  white-space: nowrap;
}
.activity-wrapper .activity-products .activity-product {
  display: inline-block;
  width: 2.54375rem;
  height: 4.19375rem;
  padding-right: 0.2rem;
  padding-top: 0.5rem;
  position: relative;
}
.activity-wrapper .activity-products .activity-product .discount {
  position: absolute;
  width: 0.90625rem;
  height: 0.90625rem;
  background: #fe4070;
  border-radius: 50%;
  color: #fff;
  line-height: 0.90625rem;
  text-align: center;
  font-size: 0.346667rems;
}
.activity-wrapper .activity-products .activity-product .product-img {
  width: 1.4875rem;
  height: 1.84375rem;
}
.activity-wrapper .activity-products .activity-product .product-img img {
  height: 100%;
}
.activity-wrapper .activity-products .activity-product .product-detail {
  padding: 0.46875rem 0px 0.21875rem 0px;
}
.activity-wrapper .activity-products .activity-product .product-detail .product-title {
  color: #000;
  font-size: 0.356667rem;
  white-space: pre-wrap;
  line-height: 0.5rem;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  width: 2.4rem;
}
.activity-wrapper .activity-products .activity-product .product-detail .price-wrap {
  padding-top: 0.3125rem;
}
.activity-wrapper .activity-products .activity-product .product-detail .price-wrap .jumei-price {
  font-size: 0.4rem;
}
.activity-wrapper .activity-products .activity-product .product-detail .price-wrap .del-price {
  color: #999;
  position: relative;
}
.activity-wrapper .activity-products .activity-product .product-detail .price-wrap .del-price:after {
  position: absolute;
  content: '';
  right: 0;
  top: 50%;
  width: 0.8rem;
  height: 1px;
  background: #999;
  -webkit-transform: scaleY(0.2);
          transform: scaleY(0.2);
}


