
.nav-wrapper {
  overflow: hidden;
  width: 100%;
}
.nav-wrapper ul {
  min-width: 118%;
  white-space: nowrap;
  margin: 0.3875rem 0.265625rem 0rem 0.341rem;
}
.nav-wrapper ul .nav-item {
  display: inline-block;
  font-size: 0.446667rem;
  min-width: 0.9rem;
  color: rgba(7,17,27,0.8);
  height: 0.5rem;
}
.nav-wrapper ul .nav-item.is-selected {
  color: #fe4070;
  padding-bottom: 0.25rem;
  border-bottom: 0.05rem solid #fe4070;
}


