
.detail_msg_img {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  overflow: hidden;
  position: absolute;
  left: 0;
  right: 0rem;
  bottom: 0.2rem;
  top: 1.27rem;
}
.detail_msg_img .detail_wrapper {
  position: relative;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.detail_msg_img .detail_wrapper img[lazy=loading] {
  display: block;
  height: 100%;
  background: #000;
  margin: auto;
}
.detail_msg_img .detail_wrapper img {
  display: block;
  width: 100%;
}


